.nav {
  position: sticky;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 3.125rem 0;
  border-bottom: 1px solid var(--border);
}
.nav #icon {
  width: 2.1875rem;
  cursor: pointer;
  margin: 0.625rem 0;
}
.nav .dev-name {
  color: var(--site-name);
  font-size: 1.375rem;
  cursor: pointer;
  font-weight: 700;
}
.nav__items {
  position: relative;
  display: flex;
  justify-content: center;
  gap: 1.5625rem;
  padding: 0;
}
@media (max-width: 39.9375em) {
  .nav__items {
    display: flex;
    flex-direction: column;
    position: fixed;
    left: -100%;
    top: 3.125rem;
    padding: 0.9375rem 0;
    text-align: center;
    justify-content: center;
    transition: all 0.3s linear;
    border-radius: 0.3125rem;
    box-shadow: 0px 1px 5px 1px var(--box-shadow);
  }
}
.nav__items.active {
  left: 1.25rem;
  right: 1.25rem;
  background-color: var(--app-background);
}
.nav__link {
  position: relative;
  color: var(--text-primary);
  font-weight: 500;
  font-size: 1.125rem;
  padding-bottom: 0.3125rem;
}
.nav__link.active {
  color: var(--active-link);
}
.nav__marker {
  position: absolute;
  left: 0;
  bottom: 0;
  height: 0.1875rem;
  width: 0;
  background-color: var(--active-link);
  transition: all 500ms;
  border-radius: 0.625rem;
}
@media (max-width: 39.9375em) {
  .nav__marker {
    display: none;
  }
}
.nav .hamburger {
  display: none;
  cursor: pointer;
}
.nav .hamburger .bar {
  display: block;
  width: 1.5625rem;
  height: 0.1875rem;
  margin: 0.3125rem auto;
  transition: transform 0.3s ease-in-out;
  background-color: var(--text-secondary);
}
.nav .hamburger.active .bar:nth-child(2) {
  opacity: 0;
}
.nav .hamburger.active .bar:nth-child(1) {
  transform: translateY(0.5rem) rotate(45deg);
}
.nav .hamburger.active .bar:nth-child(3) {
  transform: translateY(-0.5rem) rotate(-45deg);
}
@media (max-width: 39.9375em) {
  .nav .hamburger {
    display: block;
  }
}
.nav__last-child {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.625rem;
}

.intro {
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: center;
  height: calc(100vh - 3.75rem);
}
.intro__hello .wave-hand {
  background-image: url("../../../images/wave-hand.png");
  width: 2.5rem;
  height: 2.5rem;
  margin-left: 0.625rem;
}
@keyframes wave {
  from {
    transform: none;
  }
  15% {
    transform: translate3d(-20%, 0, 0) rotate3d(0, 0, 1, -10deg);
  }
  30% {
    transform: translate3d(10%, 0, 0) rotate3d(0, 0, 1, 7deg);
  }
  45% {
    transform: translate3d(-15%, 0, 0) rotate3d(0, 0, 1, -10deg);
  }
  60% {
    transform: translate3d(10%, 0, 0) rotate3d(0, 0, 1, 5deg);
  }
  75% {
    transform: translate3d(-5%, 0, 0) rotate3d(0, 0, 1, -2deg);
  }
  to {
    transform: none;
  }
}
.intro__hello .wave-hand:hover {
  animation: wave 1s;
}
.intro__hello, .intro__tagline {
  font-size: 2.5rem;
  font-weight: 300;
}
@media (max-width: 69.9375em) {
  .intro__hello, .intro__tagline {
    font-size: 2rem;
  }
}
@media (max-width: 39.9375em) {
  .intro__hello, .intro__tagline {
    font-size: 1.75rem;
  }
}
.intro__tagline {
  max-width: 50rem;
}
.intro__tagline .name {
  color: var(--text-primary);
  font-weight: 500;
}
.intro__contact {
  font-size: 1.375rem;
  font-weight: 400;
}
.intro__contact .pointer {
  background-image: url("../../../images/pointer.png");
  width: 1.875rem;
  height: 1.875rem;
  margin: 0 0.3125rem;
  vertical-align: text-bottom;
}
@media (max-width: 39.9375em) {
  .intro__contact {
    font-size: 1rem;
  }
}
.intro .highlight-link {
  transition: all 0.3s ease-in-out;
  font-weight: 400;
  text-decoration: none;
  display: inline-block;
  padding: 2px 3px;
  text-decoration: none;
  box-shadow: inset 0 -0.1875rem 0 #007bff;
  padding: 5px;
  border-radius: 0.3125rem;
  cursor: pointer;
  margin-left: 0.3125rem;
}
.intro .highlight-link:hover {
  box-shadow: inset 0 -2.375rem 0 0 #007bff;
}

.skills {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
@media (max-width: 69.9375em) {
  .skills {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 39.9375em) {
  .skills {
    grid-template-columns: 1fr;
  }
}
.skills__category-label {
  font-weight: 700;
  color: var(--text-primary);
  text-transform: uppercase;
}
.skills ul {
  padding: 0;
}
.skills__category-item {
  margin: 3px 0;
  font-size: 0.9rem;
  color: var(--text-secondary);
}

.section {
  display: flex;
  justify-content: center;
}
.section__title {
  flex-shrink: 0;
  width: 200px;
  margin-right: 70px;
  color: #007bff;
  text-transform: uppercase;
  text-align: right;
  font-weight: 700;
  letter-spacing: 2px;
}
.section__content {
  font-weight: 300;
  font-size: rem(16);
  width: 100%;
  max-width: rem(650);
}

:root {
  --selected-bg: hsl(52, 100%, 50%);
  --selected-text: hsl(240, 5%, 22%);
  --app-background: hsl(0, 0%, 100%);
  --site-name: hsl(211, 100%, 50%);
  --text-primary: hsl(240, 9%, 29%);
  --text-secondary: hsl(240, 9%, 29%);
  --active-link: hsl(167, 100%, 26%);
  --box-shadow: hsla(0, 0%, 0%, 0.2);
  --border: hsl(220, 13%, 91%);
}

body.night {
  --app-background: hsl(222, 27%, 12%);
  --text-secondary: hsl(240, 11%, 72%);
  --text-primary: hsl(0, 0%, 91%);
  --active-link: hsl(167, 100%, 33%);
  --box-shadow: hsla(0, 0%, 100%, 0.2);
  --border: hsl(0, 0%, 38%);
}

html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

*,
:after,
:before {
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
}

::-moz-selection {
  background: var(--selected-bg);
}

::selection {
  background: var(--selected-bg);
}

body,
html {
  margin: 0;
  padding: 0;
  background-color: var(--app-background);
  color: var(--text-secondary);
  font-family: "Inter", sans-serif;
  line-height: 1.5;
  width: 100%;
}

body.night ::-moz-selection,
html.night ::-moz-selection {
  color: var(--selected-text);
  background: var(--selected-bg);
}

body.night ::selection,
html.night ::selection {
  color: var(--selected-text);
  background: var(--selected-bg);
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
  outline: none;
  border: none;
}

li {
  list-style: none;
}

button {
  border: none;
}

.emoji {
  display: inline-block;
  vertical-align: text-top;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.container {
  width: 100%;
  padding: 0 clamp(1.375rem, 1.2rem + 0.89vw, 2rem);
  max-width: 70rem;
  margin: 0 auto;
}
@media (min-width: 70em) {
  .container {
    padding: 0;
  }
}